html {
    background: $light-blue;
}
header {
    background: $white;
    box-shadow: 0px 0px 12px rgba(0,0,0,0.2);
    z-index: 2;
    position: relative;
    height: 75px;
    @media (min-width: $breakpoint-medium) {
        height: 132px;
    }
}
.tm-main {
    margin-bottom: -45px;
    background: $white;
    z-index: 0;
    position: relative;
    @media (min-width: $breakpoint-medium) {
        margin-bottom: -105px;
    }
}
.tm-gray-wave {
    position: relative;
    padding-top: 11.2% !important;
    &:before {
        content: '';
        position: absolute;
        top: 0px;
        left: 0;
        width: 100%;
        height: 0;
        padding-bottom: 11.15%;
        background: $white url("/img/gray-wave.png");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
    }
    &:after {
        background: $background-gray;
    }
    .tm-gray-wave__card {
        &:nth-child(1) {
            margin-top: 20px;
        }
        &:nth-child(2) {
            margin-top: 20px;
        }
        &:nth-child(3) {
            margin-top: 20px;
        }
        @media (min-width: $breakpoint-medium) {
            &:nth-child(1) {
                margin-top: -220px;
            }
            &:nth-child(2) {
                margin-top: -150px;
            }
            &:nth-child(3) {
                margin-top: -220px;
            }
        }

    }
}
.tm-blue-wave {
    position: relative;
    padding-top: 11.2% !important;
    &:before {
        content: '';
        position: absolute;
        top: 9px;
        left: 0;
        width: 100%;
        height: 0;
        padding-bottom: 11.20%;
        background: transparent url("/img/blue-wave.png");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        @media (min-width: $breakpoint-small) {
            top: 0px;
        }
    }
    &:after {
        content: '';
        background: $blue;
    }
}
.tm-white-wave {
    position: relative;
    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 0;
        padding-bottom: 11.20%;
        background: transparent url("/img/white-wave.png");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
    }
    .uk-slideshow-items {
       /* padding-bottom: 11.2%;*/
        /*max-height: 880px;*/
        .uk-position-center {
            top: 41.8%;
        }
    }
    .uk-slidenav {
        top: 38.8%;
    }
}
.tm-section-overlap {
    margin-top: -40px;
    z-index: 0;
    @media (min-width: $breakpoint-medium) {
        margin-top: -120px;
    }
}
.tm-section-footer {
    padding-top: 70px;
    @media (min-width: $breakpoint-medium) {
        padding-top: 0px;
    }
}